<template>
   <div id="Home">
     <!-- 顶部背景 -->
     <div id="bg">
       <router-link to="/search?redirect=/" id="home" class="iconfont iconsousuo"></router-link>
     </div>
     <!-- 标签 -->
   <van-tabs v-model="active" swipeable>
      <van-tab title="最新">
              <van-pull-refresh
                v-model="isLoading"
                success-text="刷新成功"
                @refresh="onRefresh"
              >
            <div  @click="toView(item.vid)" v-for="item,i of newdata" class="temp">
                  <div class="person">
                    <p><img :src="`${url}/avatar/${item.avatar}`" alt=""></p>
                    <i v-text="item.nickname"></i>
                  </div>
                  <!-- 评论 -->
                  <p class="reivew" v-text="item.viewcontent"></p>
                  <!-- 图片 -->
              <p v-show="item.imgs" class="imgs">
                      <img v-for="itemImg,index in item.imgs" :key="index" :src="`${url}/usersimage/${item.imgs[index]}`"> 
             </p>
                  <!-- 书详情页的路径 -->
                  <div @click="toBookDetails($event,item.bid)" class="book">
                      <img :src="`${url}/coverimage/${item.coverimage}`" alt="">
                      <div>
                        <h1 v-text="item.bookname"></h1>
                        <i v-text="item.author"></i>
                      </div>
                  </div>
                  <!-- 下面的信息 -->
                  <!-- 时间 -->
                  <p class="date">{{ item.date|mydate }}</p>
            </div>
      </van-pull-refresh>
      </van-tab>
      <van-tab title="推荐">
         <van-pull-refresh
                v-model="isLoading"
                success-text="刷新成功"
                @refresh="onRefresh"
              >
            <!-- 循环生每一个介绍 -->
            <div @click="toView(item.vid)" v-for="item,i of tuijian" class="temp">
              <div class="person">
                <p><img :src="`${url}/avatar/${item.avatar}`" alt=""></p>
                <i v-text="item.nickname"></i>
              </div>
              <!-- 评论 -->
              <p class="reivew" v-text="item.viewcontent"></p>
              <!-- 图片 -->
              <p v-show="item.imgs" class="imgs">
                    <!-- 图片的循环生成 -->
                      <img v-for="itemImg,index of item.imgs" :key="index" :src="`${url}/usersimage/${itemImg}`"> 
             </p>
              <!-- 书详情页的路径 -->
              <div @click="toBookDetails($event,item.bid)" class="book">
                  <img :src="`${url}/coverimage/${item.coverimage}`" alt="">
                  <div>
                    <h1 v-text="item.bookname">毛泽东传</h1>
                    <i v-text="item.author">罗杰斯</i>
                  </div>
              </div>
              <!-- 下面的信息 -->
              <!-- 时间 -->
              <p class="date">{{ item.date|mydate }}</p>
         </div>
         <!-- 每一个循环完毕 -->
      </van-pull-refresh>
      </van-tab>
      <van-tab title="关注">
          <van-pull-refresh
                v-model="isLoading"
                success-text="刷新成功"
                @refresh="onRefresh"
              >
                <div v-if="guanzhu">
                        <div @click="toView(item.vid)" v-for="item,i of guanzhu" class="temp">
                          <div class="person">
                            <p><img :src="`${url}/avatar/${item.avatar}`"></p>
                            <i v-text="item.nickname"></i>
                          </div>
                          <!-- 评论 -->
                          <p class="reivew" v-text="item.viewcontent"></p>
                          <!-- 书详情页的路径 -->
                          <div @click="toBookDetails($event,item.bid)" class="book">
                              <img :src="`${url}/coverimage/${item.coverimage}`" alt="">
                              <div>
                                <h1 v-text="item.bookname"></h1>
                                <i v-text="item.author"></i>
                              </div>
                          </div>
                          <!-- 下面的信息 -->
                          <!-- 时间 -->
                          <p class="date">{{ item.date|mydate }}</p>
                    </div>
                </div>
                <div v-else id="att">
                    <img :src="`${url}/att.png`" alt="">
                </div>
          </van-pull-refresh>
      </van-tab>
    </van-tabs>
 <!-- tabbar -->
     <my-tabbar></my-tabbar>       
</div> 
</template>
<script>
import {viewPoint} from "../assets/js/apis/book"
import {Attentionlist} from "../assets/js/apis/user"
import {baseURL} from "../assets/js/apis/url"
import moment from "moment"
import Vue from "vue"
import {mapState} from "vuex"
Vue.filter("MyDate",(val)=>{
         let reg = /(\s)[a-z]+(?=\s)/g
        let d=moment(val).startOf('hour').fromNow()
        d=d.replace(reg,(key)=>{
            if(key==" minutes"){
                return key="分钟"
            }else if(key==" huors"){
                return key="小时"
            }else{
                return key="天"
            }
        })
        return d.replace(/\s(ago)/g,(key)=>{
            return key="前"
        })
})
export default {
  computed:{
    ...mapState([
      "uid"
    ])
  },
   data(){
     return {
       active:1,
       newdata:[],
       url:baseURL,
       tuijian:[],
       guanzhu:[],
       //是否处于加载状态
       isLoading:true
     }
   },
   methods:{
     //点击进入观点详情页
     toView(id){
       this.$router.push(`/excerpt/${id}`)
     },
     //进入书籍详情页
     toBookDetails(e,id){
         e.stopPropagation()
         this.$router.push(`/bookdetail/${id}`)
     },
     load(){
           //如果active小于2就说明点击的是最新或者推荐
          if(this.active<2){
              viewPoint().then(res=>{
                let temparr = []
              res.forEach((item)=>{
               // console.log(item)
                if(item.imgs){
                    item.imgs = item.imgs.split(",")
                     //console.log(item.imgs)
                  }
                  temparr.push(item)
                })
               this.tuijian=temparr
               //console.log(this.tuijian)
             // console.log(res)
             if(this.active==0){
                  this.newdata = temparr.sort((a,b)=>b.date-a.date)
             }
              //最新就是将数据中日期进行排序排序后的
             
                //console.log(res)
               // console.log(this.tuijian)
               this.isLoading=false
            })
          }else{
            if(!this.uid){return ;}//判断是否是登录状态
            Attentionlist(this.uid).then(res=>{
              //将返回数据保存
              //console.log(res)
              if(res!=0){
                  let temparr = []
                res.forEach((item)=>{
                  if(item.imgs){
                       item.imgs = item.imgs.split(",")
                    }
                    temparr.push(item)
                })
                this.guanzhu=temparr//如果找到了这个数据就将此给关注的数组
                this.isLoading=false//将load值成false
              }else{
                  this.guanzhu=res
              }
              
            })
          }
     },
     //下来刷新
     onRefresh(){
        this.load()
  },
     
   },
   created(){
     this.load()
      //最新的数据
   //监听active的值
   },
   watch:{
     active(){
       //如果active小于2就说明点击的是最新或者推荐
          this.load()
     }
   }
}
</script>
<style>
  #Home .van-tabs__wrap{
    position: fixed;
    top:0;
    z-index:20;
  }
  #Home .van-tab{
    margin:0 10px;
  }
  /* 背景 */
  #Home #bg{
    width:100%;
    height:60px;
    position:fixed;
    background-color:white;
    top:0;
    z-index:1;
    box-shadow:0 3px 10px 0 black;
  }

  #Home .van-tab{
    padding:10px 0 !important;
  }
  #Home .van-tabs__line{
    width:0px !important;
    height:0 !important;
  }
  #Home .van-tabs__nav.van-tabs__nav--line{
    margin:0 !important;
    padding:0 !important;
    height:60px;
  }
  #Home .van-tab__pane{
    margin-top:-35px !important;
    margin-bottom:70px;
  }
  /* 文本样式 */
  #Home .van-tab__text{
    font-size:20px;
    color:gray;
  }
  #Home .van-tabs__content{
    margin-top:100px;
  }
  #Home .van-tab.van-tab--active .van-tab__text{
    font-size:24px !important;
    font-family:"宋体";
    display: inline-block;
    width:50px;
    font-weight:bold;
    color:#743481 !important;
  }
  #Home #home.iconsousuo{
    position:absolute;
    font-size:24px;
    right:20px;
    top:calc(50% - 12px);
    color:#743481;
  }
  /* 循环模板 */
  .temp::before{
    content:"";
    display:table;
  }
  .temp{
    width:95%;
    /* border:1px solid red; */
    margin:0 auto;
    margin-top:0px;
    border-radius:5px;
    box-shadow:1px 0 5px 2px gray;
    padding-bottom:10px;
    margin-bottom:10px;
  }
  .person{
    display:flex;
    width:95%;
    margin:0 auto;
    margin-top:10px;
    align-items:center;
  }
  .person>p{
    width:35px;
    height:35px;
    border-radius:50%;
    overflow: hidden;
    /* margin-left:20px; */
  }
  .person img{
    width:35px;
    height:35px;
  }
  .person i{
    font-size:14px;
    margin-left:10px;
  }
  /* 评论 */
  .reivew{
    width:95%;
    margin:0 auto;
    margin-top:10px;
    max-height:60px;
    line-height:20px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    text-overflow:ellipsis;
    overflow: hidden;
  }
  .book{
    display:flex;
    width:95%;
    align-items:center;
    margin:0 auto;
    margin-top:10px;
    background-color:rgb(233, 233, 233);
    border-radius:3px;
  }
  .book>img{
    width:40px;
    height:50px;
  }
  .book>div{
    margin-left:10px;
  }
   .book h1{
     font-size:18px;
   }
   .book i{
     display:inline-block;
     font-size:12px;
     color:gray;
     margin-top:5px;
   }
   .date{
     width:95%;
     margin:0 auto;
     margin-top:10px;
     color:gray;
   }
   #att>img{
     width:100%;
   }
   /* 设置图片的样式 */
   #Home .imgs{
     width:100%;
   }
   #Home .imgs::after{
     content:"";
     display:block;
     clear:both;
   }
   #Home .imgs>img{
     width:30%;
     height:130px;
     margin:5px 5px;
     float:left;
   }
</style>