<template>
<!-- 搜索页面 -->
    <div id="search">
        <div id="header">
            <span @click="back" class="iconfont iconfanhui"></span>
           <input id="input" v-model="value" type="text" placeholder="请输入书名">
            <h1 @click="search">搜索</h1>
        </div>
        <!-- 内容区域 -->
        <div class="ct">
            <p v-for="i of 50">{{i}}</p>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            value:""
        }
    },
    methods:{
        back(){
            //获取从哪来的地址栏的参数
            let url = this.$route.query
            if(url.redirect){
                //点击返回在跳回哪里去
                this.$router.push(url.redirect)
            }
        },
        //绑定搜索点击事件
        search(){
            //获取
            this.$router.push(`/searchlist/${this.value}`)
        }
    }
}
</script>
<style>
#search #header{
     width:100%;
     position:fixed;
     top:0;
     background-color:white;
     height:60px;
     display:flex;
     justify-content:space-around;
     align-items:center;
 }
 #search #header .iconfanhui{
     font-size:24px;
 }
    #search #input{
        width:70%;
        height:15px;
        padding:10px 0;
        padding-left:15px;
        border-radius:20px;
        border:1px solid #743481;
    }
    #search .ct{
        margin-top:60px;
    }
</style>