<template>
    <div id="Update">
        <!-- 头部的状态 -->
        <div id="header">
            <router-link to="/person" class="iconfont iconfanhui"></router-link>
            <div>修改头像</div>
        </div>
        <van-uploader id="upload" v-model="fileList" multiple max-count="1" />
        <table></table>
        <!-- <van-uploader id="upload" image-fit="Image" :max-count="1" :data={uid:uid} :after-read="afterRead" /> -->
        <button id="up" @click="upload">确定上传</button>
    </div>
</template>
<script>
import { upload } from "../assets/js/apis/user"
import { mapState } from "vuex"
export default {
    computed:{
        ...mapState([
            "uid"
        ])
    },
    data(){
        return {
          fileList:[]
        }
    },
    methods:{
        upload(){
          let data = new FormData()
        
          data.append("file",this.fileList[0].file)
           data.append("uid",this.uid)
               upload(data).then(res=>{
                if(res==1){
                    this.$toast("上传成功")
                    //localStorage.setItem("avatar",)
                    this.$router.push("/person")
                }
            })
        },
        // afterRead(file){
        //     //console.log(file.file)
        //     let data = new FormData()
        //     let $file=file.file
        //     //console.log($file)
        //     //$file.uid=this.uid
        //  data.append("file",$file)
        //  data.append("uid",this.uid)
        //  //data.append(this.uid)
        //  //console.log(data)
        //    // data.append(this.uid)
        //     upload(data).then(res=>{
        //         if(res==1){
        //             this.$toast("上传成功")
        //             //localStorage.setItem("avatar",)
        //             this.$router.push("/person")
        //         }
        //     })
        // }
    }
}
</script>
<style>
   #Update #header{
       position: relative;;
       width:100%;
       height:60px;
       background-color:white;
       display:flex;
       align-items:center;
   }
   #header .iconfanhui{
       font-size:32px;
        align-self:center;
   }
   #Update #header>div{
       position:absolute;
       left:50%;
       transform:translate(-50%);
       font-size:20px;
   }
   /* 上传 */
#Update .van-uploader__upload{
       width:150px !important;
       height:150px !important;
       left:70% !important;
       background-color:#743481 !important;
   }
#Update .van-uploader__preview{
    margin-left:70%;
}
  #Update  .van-uploader__wrapper,#Update  .van-uploader__preview,#Update .van-image{
    /* display:block; */
     height:150px !important;
    width:150px !important;
    /* background:#0aa1ed; */
     
}
/* 点击上传的按钮 */
        #up{
            width:130px;
            height:40px;
            outline:0;
            border:0;
           margin-left:50%;
           transform:translate(-60%);
           margin-top:30px;
            background-color:#743481;
            color:white;

        }
</style>